import { useGetSectionsQuery } from "generated/graphql";
import { nanoid } from "nanoid";

export const SectionsForNav = () => {
    const { data, loading, error } = useGetSectionsQuery();

    if (loading || !data || error) return <></>;

    console.log("data :>> ", data);

    return (
        <div style={sectionsStyles}>
            <span>
                {data.getSections.map((section, i) => {
                    // section name is added for url readability
                    // section is queried by section_id; ? is added as an identifier

                    return (
                        <a
                            key={nanoid()}
                            style={sectionLinkStyles}
                            href={`#/section/${section.name}?${section.section_id}`}
                        >
                            {" "}
                            {section.name}{" "}
                            {i !== data.getSections.length - 1 ? "/" : null}
                        </a>
                    );
                })}
            </span>
        </div>
    );
};

const sectionsStyles = {
    display: "grid",
    placeItems: "center",
    height: "20px",
    backgroundColor: "white",
    boxShadow:
        "0 24px 38px 3px rgba(0,0,0,0.14),0 9px 46px 8px rgba(0,0,0,0.12),0 11px 15px -7px rgba(0,0,0,0.2)",
};

const sectionLinkStyles = {
    color: "black",
};
