import { useGetProductsQuery } from "generated/graphql";
import { nanoid } from "nanoid";
import anime from "animejs";
import { useEffect, useRef } from "react";
import "css/products.scss";

const Products = () => {
    const { data, loading, error } = useGetProductsQuery();
    const animation1Ref = useRef() as any;
    const animation2Ref = useRef() as any;

    useEffect(() => {
        animation1Ref.current = anime({
            duration: function (_t, i) {
                return 900 + i * 75;
            },
            targets: ".products-grid .card-content",
            easing: "easeOutExpo",
            // delay: function (t, i) {
            //     return i * 50;
            // },
            opacity: { value: [0, 1], easing: "linear" },
            scale: [0, 1],
            delay: anime.stagger(150, { start: 100 }),
        });

        animation2Ref.current = anime({
            duration: function (_t, i) {
                return 900 + i * 75;
            },
            targets: ".products-grid .card",
            easing: "easeOutExpo",
            opacity: { value: [0, 1], easing: "linear" },
            scale: [0, 1],
            delay: anime.stagger(150, { start: 100 }),
        });
    });

    if (loading) return <></>;

    if (!data || error) {
        return <></>;
    }

    return (
        <div className='watermark'>
            <div className='container'>
                <h1 style={{ textAlign: "center", fontFamily: "FancyHeart" }}>
                    Products
                </h1>
                <div className='products-grid'>
                    {data.getProducts.map(product => {
                        console.log("product :>> ", product);
                        return (
                            <div className='card' key={nanoid()}>
                                <a href={`#/product:${product.product_id}`}>
                                    <div className='card-image'>
                                        <img
                                            src={`${
                                                product.images![0].img_url || ""
                                            }`}
                                            alt={product.name}
                                        />
                                    </div>
                                    <div className='card-content'>
                                        <h3>{product.name}</h3>
                                        <p>
                                            $
                                            {Number(
                                                product.price / 100
                                            ).toFixed(2)}
                                        </p>
                                    </div>
                                </a>
                            </div>
                        );
                    })}
                </div>
            </div>
        </div>
    );
};

export default Products;
