import { useGetSectionsProductsQuery } from "generated/graphql";
import { Redirect } from "react-router";
import { nanoid } from "nanoid";
import "css/sections.scss";
import { SectionsForNav } from "./mobileNavbar/SectionsForNav";

const Sections = () => {
    const uri = window.location.href;
    const section_id = Number(uri.split("?")[1]);
    const section_name = uri.slice(uri.lastIndexOf("/") + 1, uri.indexOf("?"));

    const { data, loading, error } = useGetSectionsProductsQuery({
        variables: {
            section_id,
        },
    });

    if (loading) return <></>;

    if (error || !data) {
        return <Redirect to='#/' />;
    }

    console.log("data :>> ", data);

    return (
        <>
            <SectionsForNav />

            <div className='container'>
                <h1
                    style={{
                        textAlign: "center",
                    }}
                >
                    {section_name}
                </h1>

                <div className='products-grid'>
                    {data.getSectionsProducts.map(product => {
                        console.log("product :>> ", product);
                        return (
                            <div className='card' key={nanoid()}>
                                <a href={`#/product:${product.product_id}`}>
                                    <div className='card-image'>
                                        <img
                                            src={`${
                                                product.images![0].img_url || ""
                                            }`}
                                            alt={product.name}
                                        />
                                    </div>
                                    <div className='card-content'>
                                        <h4 style={{ marginBottom: "0px" }}>
                                            {product.name}
                                        </h4>
                                        <p>
                                            $
                                            {Number(
                                                product.price / 100
                                            ).toFixed(2)}
                                        </p>
                                    </div>
                                </a>
                            </div>
                        );
                    })}
                </div>
            </div>
        </>
    );
};

export default Sections;
