import Cart from "components/Cart";
import ErrorPage from "components/ErrorPage";
import ErrorPage2 from "components/ErrorPage2";
import ErrorPage3 from "components/ErrorPage3";
import Products from "components/Products";
import Sections from "components/Sections";
import { Route, Redirect, Switch } from "react-router-dom";

export const Routes = () => {
    return (
        <Switch>
            <Route path='/section' render={() => <Sections />} />
            <Route path='/products' render={() => <Products />} />
            <Route path='/cart' render={() => <Cart />} />
            <Route path='/error-1' render={() => <ErrorPage />} />
            <Route path='/error-2' render={() => <ErrorPage2 />} />
            <Route path='/error-3' render={() => <ErrorPage3 />} />
            <Route render={() => <Redirect to='/products' />} />
        </Switch>
    );
};
