import { HashRouter as Router } from "react-router-dom";
import Navbar from "components/mobileNavbar/Navbar";
import "./css/app.scss";
import { Routes } from "routes/Routes";

function App() {
    return (
        <Router>
            <div className='App'>
                <Navbar />
                <Routes />
            </div>
        </Router>
    );
}

export default App;
